  margin-top: 0;
  margin-bottom: 0;
  line-height: 0; }
  .wp-block-image.img-overflow-top figure,
  .wp-block-image.img-overflow-bottom figure {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 0; }
  .wp-block-image.img-overflow-top img,
  .wp-block-image.img-overflow-bottom img {
